<template>
  <div v-if="service" class="panel" :style="panelStyling">
    <v-panel-header @refresh="startRefresh" :loading="loading" :scrollTop="scrollTop" :service="service"></v-panel-header>
    <div class="panel--content" :style="panelContentStyling" @scroll="onScroll">
      <transition name="slide">
        <v-panel-error @refresh="startRefresh" v-if="service.error === 'true'" :serviceId="serviceId" :serviceName="service.name"></v-panel-error>
      </transition>
      <component v-for="(component, index) in components" :key="index" :is="component.name" :props="component.props"></component>
      <v-loading-item v-if="loadingMore"></v-loading-item>
    </div>
    <v-service-actions :service="service"></v-service-actions>
  </div>
</template>

<style src="css/v-panel.scss"></style>

<script>
  import { mapGetters } from 'vuex';
  import dynamicImportComponent from 'modules/dynamic-import-component';
  import vPanelHeader from 'components/v-panel-header';

  export default {
    components: {
      vPanelHeader,
      vPanelSubheader: dynamicImportComponent('v-panel-subheader'),
      vPanelError: dynamicImportComponent('v-panel-error'),
      vPanelItem: dynamicImportComponent('v-panel-item'),
      vPanelImage: dynamicImportComponent('v-panel-image'),
      vServiceActions: dynamicImportComponent('v-service-actions'),
      vLoadingItem: dynamicImportComponent('v-loading-item')
    },

    props: {
      serviceId: Number
    },

    data () {
      return {
        loading: false,
        loadingMore: false,
        scrollTop: 0
      };
    },

    computed: {
      panelStyling() {
        return {
          width: this.service.panelWidth + 'px',
        };
      },
      panelContentStyling () {
        return {
          'padding-bottom': this.service.actions.length > 0 ? '50px' : 0
        };
      },
      components () {
        if (this.service.components) {
          return JSON.parse(this.service.components);
        } else {
          return {};
        }
      },
      nextPage () {
        const items = this.components.filter(component => component.name === 'v-panel-item' || component.name === 'v-panel-image');
        return Math.ceil(items.length / this.service.perPage + 1);
      },
      service () {
        return this.activeServices.find((service) => service.id === this.serviceId);
      },
      ...mapGetters(['activeServices'])
    },

    mounted () {
      chrome.runtime.onMessage.addListener((message) => {
        if (message.name === 'finishRefresh') {
          this.loading = false;
          this.loadingMore = false;
        }
      });
    },

    methods: {
      onScroll (event) {
        this.scrollTop = event.target.scrollTop;

        // TODO: Prevent load more when everything is loaded.
        // How do I know whether everything is loaded?
        if (!this.service.loadMore || this.loading) return;

        const maxScrollTop = event.target.scrollHeight - event.target.offsetHeight;
        const spaceToEnd = maxScrollTop - this.scrollTop;
        if (spaceToEnd < 10) {
          this.startRefresh(this.nextPage);
        }
      },
      startRefresh (page) {
        this.loading = true;
        if (page > 1) this.loadingMore = true;

        chrome.runtime.sendMessage({
          name: 'startRefresh',
          serviceId: this.serviceId,
          page
        });
      }
    }
  };
</script>



